@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
}
body {
  background: url(/portfolio/static/media/stars.9b64a25d.jpg) center;

  font-family: "Montserrat";
}
body::-webkit-scrollbar {
  width: 0.2rem;
}
body::-webkit-scrollbar-track {
  background: black;
}
body::-webkit-scrollbar-thumb {
  background: #23232e;
  background: var(--bg-primary);
  border-radius: 30px;
  height: 10px;
}
/* Small screens */
@media only screen and (max-width: 600px) {
  main.aboutMe {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container {
    margin-top: 20px;
    width: 80%;
    height: 75vh;
    color: white;
    background-color: rgba(35, 35, 46, 0.9);
    display: flex;
    flex-direction: column;
    border-radius: 13px;
  }
  .wrapper {
    padding: 25px;
    display: flex;
    flex-direction: column;
  }
  .wrapper h1 {
    font-size: 13px;
  }
  .imgBox img {
    width: 150px;
  }
  .aboutBox {
    padding: 13px;
  }

  .aboutBox p {
    font-size: 12px;
  }
  nav.navbar {
    top: 0;
    width: 100%;
    height: 5rem;
    z-index: 3;
  }

  ul.navbar-nav {
    display: flex;
    flex-direction: row;
  }

  main.aboutMe {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    margin: 0;
  }
  main.contactMain,
  main#projectsMain,
  main.technologies {
    margin: 0;
  }

  div.tech-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .logo {
    display: none;
  }

  .nav-link {
    justify-content: center;
  }
}

/* Large screens */
@media only screen and (min-width: 600px) {
  .navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .navbar:hover {
    width: 16rem;
  }
  .navbar:hover ~ main {
    padding-left: 185px;
  }

  .navbar:hover .link-text {
    display: inline;
    transition: 3s;
  }

  .navbar:hover .logo svg {
    margin-left: 11rem;
  }

  .navbar:hover .logo-text {
    left: 0px;
  }
  .container {
    width: 1024px;
    height: 500px;
    color: white;
    background-color: rgba(35, 35, 46, 0.9);
    display: flex;
    border-radius: 13px;
  }
  .wrapper {
    width: 50%;
    border-radius: 13px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .aboutBox {
    width: 50%;
    border-radius: 13px;

    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .aboutBox h1 {
    padding: 20px;
  }
  .imgBox img {
    width: 200px;
  }
}

.dark {
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
}

.light {
  --text-primary: #1f1f1f;
  --text-secondary: #000000;
  --bg-primary: #ffffff;
  --bg-secondary: #e4e4e4;
}

.solar {
  --text-primary: #576e75;
  --text-secondary: #35535c;
  --bg-primary: #fdf6e3;
  --bg-secondary: #f5e5b8;
}

.theme-icon {
  display: none;
}

.dark #darkIcon {
  display: block;
}

.light #lightIcon {
  display: block;
}

.solar #solarIcon {
  display: block;
}

main.aboutMe {
  min-height: 105vh;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.aboutBox {
  padding: 20px;
}
@-webkit-keyframes drop {
  0% {
    top: -30px;
    opacity: 0;
  }
  100% {
    top: 0;
    opacity: 1;
  }
}
@keyframes drop {
  0% {
    top: -30px;
    opacity: 0;
  }
  100% {
    top: 0;
    opacity: 1;
  }
}

.tech-container {
  margin-top: 70px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.card {
  position: relative;
  max-width: 300px;
  height: 215px;
  margin: 30px 10px;
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  box-shadow: 5px 12px 11px -1px rgba(0, 0, 0, 0.75);
  transition: 0.3s ease-in-out;
  font-size: 12px;
  background-color: #fff;
  -webkit-animation: drop ease-in-out 0.3s;
          animation: drop ease-in-out 0.3s;
}
.card:hover {
  height: 420px;
  cursor: pointer;
}
.card .imgBox {
  position: relative;
  display: flex;
  width: 230px;
  height: 220px;
  /* background-color: blue; */
  top: -60px;
  left: 20px;
  z-index: 1;
}
.card .imgBox img {
  max-width: 230px;
  height: 220px;
  margin: 0 auto;

  border-radius: 4px;
}
.card .content {
  /* background-color: red; */
  position: relative;
  margin-top: -140px;
  padding: 10px 15px;
  text-align: center;
  visibility: hidden;
  transition: 0.2s ease-in-out;
  flex-direction: column;
  display: flex;
  align-items: center;
}
.card:hover .content {
  visibility: visible;
  opacity: 1;
  margin-top: -40px;
  transition-delay: 0.3s;
}
.card .content h1 {
  font-size: 20px;
}

a.cardButton {
  margin-top: 25px;
  padding: 5px 12px;
  border: 3px solid #2a2d34;
  color: #2a2d34;
  background: transparent;
  text-decoration: none;
  transition: 0.1s ease-in-out;
}
a.cardButton:hover {
  margin-top: 25px;
  padding: 5px 12px;
  border: 3px solid #2a2d34;
  color: white;
  background: #2a2d34;
  text-decoration: none;
}


#projectsMain {
  background: url(/portfolio/static/media/stars.9b64a25d.jpg) center;

  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contactMain {
  background: url(/portfolio/static/media/stars.9b64a25d.jpg) center;

  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

:root {
  font-size: 16px;
  font-family: "Open Sans";
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --transition-speed: 500ms;
}

body {
  color: black;
  background-color: white;
  margin: 0;
  padding: 0;
  text-align: center;
}

main {
  margin-left: 5rem;
  padding: 1rem;
}

.navbar {
  position: fixed;
  background-color: #23232e;
  background-color: var(--bg-primary);
  transition: width 600ms ease;
  height: 100vh;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
}

.nav-item:last-child {
  justify-content: center;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: #b6b6b6;
  color: var(--text-primary);
  text-decoration: none;
  -webkit-filter: grayscale(100%) opacity(0.7);
          filter: grayscale(100%) opacity(0.7);
  transition: 500ms;
  transition: var(--transition-speed);
}

.nav-link:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  background: #141418;
  background: var(--bg-secondary);
  color: #ececec;
  color: var(--text-secondary);
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.nav-link svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.fa-primary {
  color: #9d8df1;
}

.fa-secondary {
  color: #b8cdf8;
}

.fa-primary,
.fa-secondary {
  transition: 500ms;
  transition: var(--transition-speed);
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: #ececec;
  color: var(--text-secondary);
  background: #141418;
  background: var(--bg-secondary);
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: 500ms;
  transition: var(--transition-speed);
}
.logo-text {
  font-size: 17px;
  transition: 0.8s !important;
}
.logo-text {
  display: inline;
  position: absolute;
  left: -999px;
  transition: 500ms;
  transition: var(--transition-speed);
}

.navbar:hover .logo svg {
  transform: rotate(-180deg);
}

main {
  transition: 0.5s;
}

